.calc {
  background: var(--bg-color);
  color: var(--primary-font-color);
  border-radius: 19px;
  grid-template-rows: repeat(7, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "toggle toggle toggle toggle"
                       "display display display display"
                       "c signed percent divide"
                       "seven eight nine times"
                       "four five six minus"
                       "one two three plus"
                       "zero zero decimal equals";
  place-items: center;
  gap: 25px;
  padding: 40px;
  display: grid;
  box-shadow: 0 0 16px #0000001a;
}

.display {
  box-shadow: 7px 7px 16px 1px var(--dark-highlight), -7px -7px 16px 1px var(--soft-highlight), inset 7px 7px 9px 0 var(--dark-highlight), inset -7px -7px 9px 0 var(--soft-highlight);
  border: 5px solid var(--soft-high-light);
  color: var(--secondary-font-color);
  text-align: right;
  letter-spacing: 3px;
  border-radius: 12px;
  width: 100%;
  height: 60px;
  padding-right: 18px;
  font-size: 30px;
  line-height: 60px;
}

.neumorphic {
  box-shadow: 7px 7px 16px 1px var(--dark-highlight), -7px -7px 17px 0 var(--soft-highlight);
  border: 1px solid var(--soft-highlight);
  border-radius: 100%;
  transition: all .1s ease-in-out;
}

.neumorphic:hover {
  box-shadow: inset 7px 7px 6px 0 var(--dark-highlight), inset -7px -7px 6px 0 var(--soft-highlight);
  color: var(--secondary-font-color);
}

