  height: 22px;
  position: absolute;
  bottom: 4px;
  left: 4px;
  box-shadow: 0 0 6px #0003;
}

input:checked + .slider {
  background-color: var(--bg-color);
  box-shadow: 7px 7px 16px 1px var(--dark-highlight), -7px -7px 16px 1px var(--soft-highlight), inset 7px 7px 9px 0 var(--dark-highlight), inset -7px -7px 9px 0 var(--soft-highlight);
}

input:checked + .slider:before {
  background-color: var(--secondary-font-color);
  transform: translateX(26px);
}

